    computed: {
    },
    methods: {
    },
    components: {
        ue
    },
    created () {
    }
}
</script>>
<style scoped>
.lesson .title{
    margin: 0;
    padding: 15px 0;
    border-bottom: 1px solid rgb(254, 145, 80);
    font-size: 14px;
    text-align: center
}
